body {
  background-color: #f8f8f2;
  color: #161715; }

a {
  color: darkred; }

:not(pre) > code {
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  color: darkred;
  word-wrap: break-word; }

img {
  max-width: 100%; }

ul {
  list-style: square; }

hr {
  border: 0;
  border-top: 1px solid #c9cbc6; }

footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0; }
  footer ul {
    text-align: center;
    margin-bottom: 2rem; }
  footer li {
    display: inline;
    margin: 1rem; }

html {
  min-height: 100%;
  position: relative; }

main {
  padding-bottom: 60px; }

.dotted-border {
  border: 1px dotted #969a91; }

.float-right {
  float: right; }

.float-left {
  float: left; }

.clearfix::after {
  content: "";
  clear: both;
  display: table; }

.page--index ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.page--index .max-2-col li {
  flex: 200px; }

.page--index .max-3-col li {
  flex: 150px; }

.page--journal ul {
  padding: 0;
  list-style: none;
  overflow-x: hidden;
  position: relative;
  line-height: 3rem; }
  .page--journal ul a,
  .page--journal ul span {
    background-color: #f8f8f2; }
  .page--journal ul span {
    margin-left: 1rem; }
  .page--journal ul a::after,
  .page--journal ul span::before {
    content: "..............................................................................................................................................................................................................";
    position: absolute;
    z-index: -1;
    color: #969a91; }
  .page--journal ul span::before {
    right: 0;
    background-color: #f8f8f2; }
  .page--journal ul a::after {
    left: 0; }

.journal-entry--date,
.journal-entry--codepen-link {
  font-size: 90%;
  line-height: 1rem; }

.journal-entry--codepen-link {
  float: right; }

/**
 * Monokai theme for Prism.JS
 *
 * @author Martijn Swaagman
 * @license MIT 2015
 */
code[class*="language-"],
pre[class*="language-"] {
  color: #f8f8f2;
  text-shadow: 0 1px rgba(0, 0, 0, 0.3);
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none; }

pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
  border-radius: 0.3em; }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #272822; }

:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em; }

.token.comment,
.token.doctype {
  color: #778090; }

.token.punctuation {
  color: #F8F8F2; }

.token.property,
.token.tag {
  color: #F92672; }


.token.number {
  color: #AE81FF; }

.token.selector,
.token.attr-name,
.token.string {
  color: #A6E22E; }

.token.operator,
.language-css .token.string,
.style .token.string,
.token.variable {
  color: #F8F8F2; }


.token.attr-value,
.token.function {
  color: #E6DB74; }

.token.keyword {
  color: #F92672; }


.token.important {
  color: #FD971F; }

.token.important {
  font-weight: bold; }

code[class*="language-"],
pre[class*="language-"] {
  color: #f8f8f2;
  text-shadow: 0 1px rgba(0, 0, 0, 0.3);
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5rem;
  tab-size: 4;
  hyphens: none; }

pre[class*="language-"] {
  position: relative;
  padding: 1rem 1rem 1rem 3rem;
  margin: 1rem 0;
  overflow: auto;
  border-radius: 0;
  counter-reset: line; }

code[class*="language-"] span.highlight-line:before {
  counter-increment: line;
  content: counter(line);
  text-align: right;
  width: 2rem;
  position: absolute;
  left: 0;
  opacity: .2; }

.token.keyword {
  color: #59eeff; }

.token.property,
.token.tag,
.token.operator {
  color: #ff3d6e; }

.token.string {
  color: #ff8041; }
